import React from 'react';
import { /* Button, */ Card, CardBody, CardSubtitle, CardText } from 'reactstrap';
import { NavLink } from 'react-router-dom';
import img from "../../assets/img/profiles/profile.png";

const SubmissionCard = ({ data }) => {
  let isImg = false;
  if(data?.profile_photo?.match(/\.(jpeg|jpg|gif|png)$/) !== null)
  {
    isImg = true;
  }
  return (
    <Card className="d-flex flex-row mb-4">
      <NavLink to={data.url} className="d-flex">
        <img src={isImg?data.profile_photo:img} alt="Not Found" width="100px" className="m-4" />
      </NavLink>
      <div className=" d-flex flex-grow-1 min-width-zero">
        <CardBody className=" pl-0 align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero">
          <div className="min-width-zero">
            <NavLink to={data.url}>
              <CardSubtitle className="truncate mb-1">{data.full_name}</CardSubtitle>
            </NavLink>
            <CardText className="text-muted text-small mb-2">
              {data.contact_no}
            </CardText>
            <CardText className="theme-color-blueyale mb-2">
              <a href={data.url} className="theme-color-bluenavy">Solution Link</a>

            </CardText>
            <CardText className=" mb-2">
              <a href={data.video_file} className="theme-color-bluenavy">Solution video File</a>
            </CardText>
            {/* <Button outline size="xs" color="danger">
              Remove
            </Button> */}
          </div>
        </CardBody>
      </div>
    </Card>
  );
};

export default SubmissionCard;
