import React from 'react';
import { Card, CardBody, CardTitle } from 'reactstrap';
import { NavLink } from 'react-router-dom';
// import ParticipantCard from '../../../../../components/cards/ParticipantCard';

import IntlMessages from '../../../../../helpers/IntlMessages';

const ParticipantList = ({ data, match }) => {
  return (
    <>
      <Card className="mb-4">
        <CardBody>
          <CardTitle className="h1 mb-0 ">
            <div className="d-flex justify-content-between ">
              <IntlMessages id="challenges.participants" />
              {/* <pre>{JSON.stringify(data, null, 2)}</pre> */}

              {data.length ? (
                <>
                  {`  (${data.length})`}
                  <NavLink to={`${match.url}/participants`} className="p-0 m-0">
                    See all <i className="simple-icon-arrow-right-circle m-2" />
                  </NavLink>
                </>
              ) : null}
            </div>
          </CardTitle>
        </CardBody>
      </Card>

      {data.length ? null : (
        <Card className="mb-4">
          <CardBody>
            <CardTitle className="h1 mb-0">Currently No Participants</CardTitle>
          </CardBody>
        </Card>
      )}
    </>
  );
};

export default ParticipantList;
