import React from 'react';

import { Card, CardBody, NavLink } from 'reactstrap';
import IntlMessages from '../../../../../helpers/IntlMessages';

const HackDetails = ({ hackInfo }) => {
  const { hackathon } = hackInfo;

  return (
    <Card className="mb-4">
      <CardBody>
        <div className="text-center pt-4">
          <p className="list-item-heading pt-2">{hackathon.name}</p>
        </div>
        <p className="mb-3">{hackathon.about}</p>
        <p className="text-muted text-small mb-2">
          {/* <IntlMessages id="pages.location" /> */}
          Tagline
        </p>
        <p className="mb-3">{hackathon.tagline}</p>
        {/* <p className="text-muted text-small mb-2">
          <IntlMessages id="pages.responsibilities" />
        </p>
        <p className="mb-3">
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            FRONTEND
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            JAVASCRIPT
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            SECURITY
          </Badge>
          <Badge color="outline-secondary" className="mb-1 mr-1" pill>
            DESIGN
          </Badge>
        </p> */}
        <p className="text-muted text-small mb-2">
          <IntlMessages id="menu.contact" />
        </p>
        <div className="social-icons">
          <ul className="list-unstyled list-inline">
            <li className="list-inline-item">
              <NavLink href={hackathon.facebook} target="_blank">
                <i className="simple-icon-social-facebook" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink href={hackathon.twitter} target="_blank">
                <i className="simple-icon-social-twitter" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink href={hackathon.instagram} target="_blank">
                <i className="simple-icon-social-instagram" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink href={hackathon.linkedin} target="_blank">
                <i className="simple-icon-social-linkedin" />
              </NavLink>
            </li>
            <li className="list-inline-item">
              <NavLink href={hackathon.hackathon_website} target="_blank">
                <i className="simple-icon-link" />
              </NavLink>
            </li>
          </ul>
        </div>
      </CardBody>
    </Card>
  );
};

export default HackDetails;
