import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import Lightbox from 'react-image-lightbox';
import img from "../../assets/img/profiles/profile.png";

const SingleLightbox = ({ thumb, className, large }) => {
  const [isOpen, setIsOpen] = useState(false);
  let isImg = false;
  if(thumb !== null)
  {
    isImg = true;
  }
  console.log(thumb);
  return (
    <>
      <NavLink to="#" location={{}} onClick={() => setIsOpen(true)}>
        <img 
          alt="Not Found"
          src={isImg?thumb:img}
          className={className} 
          width="500" 
        />
      </NavLink>
      {isOpen && (
        <Lightbox mainSrc={large} onCloseRequest={() => setIsOpen(false)} />
      )}
    </>
  );
};
export default SingleLightbox;
